import React, { useEffect, useRef } from "react";
import { useNavigate } from "react-router-dom";
import * as S from "./style";

import Input from "../../Components/Inputs/Inputs";
import Button from "../Buttons/Buttons";
import Head from "../Helper/Head/Head";
import Error from "../Helper/Error/Error";

import { PHOTO_POST } from "../../services/apiUrl";
import useFetch from "../../Hooks/useFetch";
import useForm from "../../Hooks/useForm";

const UserPhotoPost = (finishState) => {
  const picture_name = useForm();
  const description = useForm();
  const type_pic = useForm();
  const price = useForm("number");
  const { data, error, loading, request } = useFetch();
  const navigate = useNavigate();

  useEffect(() => {
    if (data) navigate("/account");
  }, [data, navigate]);

  const fool = useRef(null);

  const cursorLayer = document.getElementById("image_finish");
  const canvas_content = document.getElementById("canvas_content");

  var blobPromise = function getCanvasBlob(canvas) {
    return new Promise(function (resolve, reject) {
      canvas.toBlob(
        function (blob) {
          resolve(blob);
        },
        "image/jpeg",
        0.9
      );
    });
  };

  var canvasBlob = blobPromise(cursorLayer);

  canvasBlob.then(
    function (blob) {
      fool.current = blob;
    },
    function (err) {
      console.log(err);
    }
  );

  const handleSubmit = (event) => {
  
    var formData = new FormData();

    event.preventDefault();

    formData.append(
      "img",
      new File([fool.current], "my_image.jpg", {
        type: "image/jpeg",
        lastModified: new Date().getTime(),
      })
    );
    formData.append("picture_name", picture_name.value);
    formData.append("description", description.value);
    formData.append("price", 12);
    formData.append("type", type_pic.value);
    const token = window.localStorage.getItem("token");
    const { url, options } = PHOTO_POST(formData, token);
    request(url, options);    
  };

  return (
    <S.PhotoPost>
      <Head title="Create picture" />
      <form onSubmit={handleSubmit}>
        <Button disabled={loading ? true : false} className="button-create">
          {loading ? "Loading..." : "Create"}
        </Button>

        <Input label="Name" type="text" name="picture_name" {...picture_name} />
        <Input
          label="Description"
          type="textarea"
          name="description"
          {...description}
        />
        <Input label="Type" type="text" name="type_pic" placeholder="paint, pencil etc." {...type_pic} />
        <Input label="Price $" type="number" name="price" placeholder="12$" value="12" disabled/>
        

        <Error error={error} />
      </form>
    </S.PhotoPost>
  );
};

export default UserPhotoPost;
