import React, { useEffect, useContext, useState } from "react";
import { NavLink, useLocation } from "react-router-dom";
import * as S from "./style";

import { ReactComponent as MinhasFotos } from "../../Assets/feed.svg";
import { ReactComponent as Statistics } from "../../Assets/statistics.svg";
import { ReactComponent as AddFoto } from "../../Assets/adicionar.svg";
import { ReactComponent as Logout } from "../../Assets/sair.svg";

import { UserContext } from "../../UserContext";

const UserHeaderNav = () => {
  const [mobileMenu, setMobileMenu] = useState(false);
  const { pathname } = useLocation();
  const { userLogout } = useContext(UserContext);
  const [ mobile, setMobile] = useState(false);

  useEffect(() => {
    setMobileMenu(false);
    
      if (window.innerWidth < 700) { setMobile(true) }
      else {
        setMobile(false)
      }
  }, [pathname]);

  //const mobile = useMedia("(max-width: 50rem)");

  return (
    <>
      {mobile && (
        <S.MobileButton
          aria-label="menu"
          className={mobileMenu && "mobile__button__active"}
          onClick={() => setMobileMenu(!mobileMenu)}
        />
      )}

      <S.Nav
        className={`${mobile ? "menu__mobile__items" : ""} ${
          mobileMenu ? "mobile__items__active" : ""
        }`}
      >
        <NavLink to="/account" end>
          <MinhasFotos />
          {mobile && "My Pictures"}
        </NavLink>
        <NavLink to="/account/statistics">
          <Statistics />
          {mobile && "Statistics"}
        </NavLink>
        <NavLink to="/account/create">
          <AddFoto />
          {mobile && "Add Picture"}
        </NavLink>
        <button onClick={userLogout}>
          <Logout />
          {mobile && "Logout"}
        </button>
      </S.Nav>
    </>
  );
};

export default UserHeaderNav;
