export const PHOTO_POST = (formData, token) => {
  return {
    url: `${API_URL}/photo`,
    options: {
      method: "POST",
      headers: {
        Authorization: "Bearer " + token,
      },
      body: formData,
    },
  };
};

export const PHOTO_FILTER_POST = (formData, token) => {
  return {
    url: `${API_URL}/photo_filter`,
    options: {
      method: "POST",
      headers: {
        Authorization: "Bearer " + token,
      },
      body: formData,
    },
  };
};

export const PHOTOS_GET = ({ page, total, user }) => {
  return {
    url: `${API_URL}/photo/${page}/${total}/${user}`,
    options: {
      method: "GET",
      cache: "no-store",
    },
  };
};

export const PHOTO_GET = id => {
  return {
    url: `${API_URL}/photo/${id}`,
    options: {
      method: "GET",
      cache: "no-store",
    },
  };
};

export const COMMENT_POST = (id, body) => {
  body.id = id;
  return {
    url: `${API_URL}/comment`,
    options: {
      method: "POST",
      cache: "no-store",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + localStorage.getItem("token"), // apenas um exemplo
      },      
      body: JSON.stringify(body),
    },
  };
};

