import React, { useEffect } from "react";
import * as S from "./style";

import { PHOTOS_GET } from "../../services/apiUrl";
import FeedPhotosItem from "./FeedPhotosItem";
import useFetch from "../../Hooks/useFetch";

import Error from "../Helper/Error/Error";
import Loading from "../Helper/Loading/Loading";

var isempty = '';

const FeedPhotos = ({ setInfinite, page, user, setModalPhoto }) => {
  const { data, loading, error, request } = useFetch();
  useEffect(() => {
    const fetchPhotos = async () => {
      const total = 9;
      const { url, options } = PHOTOS_GET({ page, total, user });
      const { response, json } = await request(url, options).then((response, json) => {
        
        isempty = '';
        if (response.json.length < 1 && page === 1) {
          isempty = "You don't have any pictures at the moment";
        }  
        return response;
      });
      if (response && response.ok && json.length < total) setInfinite(false);
        
    };
    fetchPhotos();
    //isempty = data;    
  }, [page, request, setInfinite, user]);

  if (error) return <Error error={error} />;  
  if (loading) return <Loading />;
  if (isempty !== '' && user !== 0) return (<div>{isempty}</div>)  
  return (
    
    <S.Feed>     
      {data?.map(photo => (
        <FeedPhotosItem
          setModalPhoto={setModalPhoto}
          key={photo.id}
          photo={photo}
        />
      ))}
    </S.Feed>
  );
};

export default FeedPhotos;
