import React, { useContext } from "react";
import * as S from "./style";

import { USER_POST } from "../../services/apiUrl";
import { UserContext } from "../../UserContext";

import useForm from "../../Hooks/useForm";
import useFetch from "../../Hooks/useFetch";

import { Error } from "../Inputs/style";
import Button from "../Buttons/Buttons";
import Input from "../Inputs/Inputs";
import Title from "../Title/Title";
import Head from "../Helper/Head/Head";

const Register = () => {
  const username = useForm();
  const email = useForm("email");
  const password = useForm(); // if used in real application, force type "password". Make regex in Hook.
  const age = useForm(); // if used in real application, force type "password". Make regex in Hook.
  const social_profile = useForm("social_profile"); // if used in real application, force type "password". Make regex in Hook.

  const { userLogin } = useContext(UserContext);
  const { loading, error, request } = useFetch();

  const handleSubmit = async event => {
    event.preventDefault();
    const { url, options } = USER_POST({
      name: username.value,
      email: email.value,
      age: age.value,
      social_profile: social_profile.value,
      password: password.value,
    });
    const { response } = await request(url, options);
    if (response.ok) userLogin(username.value, password.value);
  };

  return (
    <S.Section>
      <Head title="Create account" />
      <Title sectionTitle="Create account">Create account</Title>
      <S.LoginFormStyled onSubmit={handleSubmit}>
        <Input label="Name" type="text" name="username" {...username} />
        <Input label="Email" type="text" name="email" {...email} />
        <Input label="Age" type="text" name="age" {...age} />
        <Input label="Social profile" type="text" name="social_profile" {...social_profile} placeholder="example: https://twitter.com/kidsnftspace"/>
        <Input label="Password" type="password" name="password" {...password} />
        <Button disabled={loading ? true : false}>
          {loading ? "Registration..." : "Create"}
        </Button>
        {error && <Error>{error}</Error>}
      </S.LoginFormStyled>
    </S.Section>
  );
};

export default Register;
