import React, { useContext } from "react";
import * as S from "./style";

import { Link } from "react-router-dom";

import { UserContext } from "../../UserContext";
import { TOKEN_POST } from "../../services/apiUrl";

import Input from "../Inputs/Inputs";
import Buttons from "../Buttons/Buttons";
import Title from "../Title/Title";
import { Error } from "../Inputs/style";
import Head from "../Helper/Head/Head";

import useForm from "../../Hooks/useForm";

const LoginForm = () => {
  const username = useForm();
  const password = useForm();

  const { userLogin, error, loading } = useContext(UserContext);

  const handleSubmit = async event => {
    event.preventDefault();
    if (username.validate() && password.validate()) {
      userLogin(username.value, password.value);

      /* const { url, options } = TOKEN_POST({
        name: username.value,
        password: password.value,
      });

      const response = await fetch(url, options);
      const json = await response.json();
      localStorage.setItem("token", json.token); */
    }
  };

  return (
    <S.Section>
      <Head title="Login" />
      <Title sectionTitle="Login">Login</Title>
      <S.LoginFormStyled onSubmit={handleSubmit}>
        <Input
          label="Name"
          name="username"
          type="text"
          value={username}
          {...username}
        />
        <Input
          label="Password"
          name="password"
          type="password"
          value={password}
          {...password}
        />
        <Buttons type="submit" disabled={loading ? true : false}>
          {loading ? "Enter..." : "Enter"}
        </Buttons>
        {error && <Error>{error}</Error>}
      </S.LoginFormStyled>

     {/*  <Link to="/login/passport_lost" className="password__lost">
      Forgot your password?
      </Link> */}

      <S.Register>
        <h2>Sign In</h2>
        <p>Create your Kids Account.</p>

        <Link to="/login/sign" className="login__register">
          <Buttons type="submit">Join us</Buttons>
        </Link>
      </S.Register>
    </S.Section>
  );
};

export default LoginForm;
