import React, { useContext } from "react";
import { Link } from "react-router-dom";
import * as S from "./style";

import Image from "../Helper/Image/Image";
import PhotoDelete from "./PhotoDelete";
import Title from "../Title/Title";
import PhotoComments from "./PhotoComments";
import Ipfs from "../../Assets/logo_network/logo_ipfs_min.png";
import IpfsIn from "../../Assets/logo_network/logo_ipfs_min_in.png";
import Rarible from "../../Assets/logo_network/logo_rarible_min.png";
import RaribleIn from "../../Assets/logo_network/logo_rarible_min_in.png";
import Opensea from "../../Assets/logo_network/logo_opensea_min.png";
import OpenseaIn from "../../Assets/logo_network/logo_opensea_min_in.png";
import Omniflix from "../../Assets/logo_network/logo_omniflix_min_in.png";
import Stargaze from "../../Assets/stargaze.png";
import MagicEden from "../../Assets/magic_eden.png";

import { UserContext } from "../../UserContext";

const PhotoContent = ({ data, single }) => {
  const { photo, comments } = data;
  const user = useContext(UserContext);

  return (
    <S.Photo className={single && "photo__single"}>
      <S.PostImage>
        <S.SingleImage><Image src={photo.src} alt={photo.title} /></S.SingleImage>
        
      </S.PostImage>
      <S.Details>
        <div>
          <S.Author>
            {user.data && user.data.username === photo.author ? (
              <PhotoDelete id={photo.id}></PhotoDelete>
            ) : (
              <Link to={`/profile/${photo.author_id}`}>@{photo.author}</Link>
            )}
            <S.Views>{photo.count_views}</S.Views>
          </S.Author>
          <Link to={`/foto/${photo.id}`}>
            <Title sectionTitle={photo.title}>{photo.title}</Title>
          </Link>
          <S.Attributes>
            <li>~{photo.price}$</li>
            {photo.ipfs === null ? (
              <img src={IpfsIn} alt="Ipfs" width={20} height={20} />              
            ) : (
              <a href={`${photo.ipfs}`} target="_blank"><img src={Ipfs} alt="Ipfs" width={20} height={20} /></a>
            )}
            {photo.rarible === null ? (
              <img src={RaribleIn} alt="Rarible" width={20} height={20} />              
            ) : (
              <a href={`${photo.rarible}`} target="_blank"><img src={Rarible} alt="Rarible" width={20} height={20} /></a>
            )}
            {photo.opensea === null ? (
              <img src={OpenseaIn} alt="Opensea" width={20} height={20} />              
            ) : (
              <a href={`${photo.opensea}`} target="_blank"><img src={Opensea} alt="Opensea" width={20} height={20} /></a>
            )}           
            <img src={Omniflix} alt="Omniflix" width={20} height={20} />
            <img src={Stargaze} alt="Stargaze" width={20} height={20} />
            <img src={MagicEden} alt="MagicIden" width={20} height={20} />        
          </S.Attributes>
        </div>
      </S.Details>
      <PhotoComments id={photo.id} comments={comments} />
    </S.Photo>
  );
};

export default PhotoContent;
