import React, { useCallback, useRef, useState } from 'react'
import { Upload } from 'antd'
import { AiOutlineCheck, AiOutlineCamera } from "react-icons/ai";
import Button from "../Buttons/Buttons";
import Cropper from 'react-perspective-cropper'
import Loading from "../Helper/Loading/Loading";
import { checkImageWidth } from "../Helper/Image/checkImageWidth";
import { resizeImage } from "../Helper/Image/resizeImage";

import UserPhotoPost from "./UserPhotoPost";
import UserPhotoFilter from "./UserPhotoFilter";

import './App.css'

const { Dragger } = Upload

var file_k;

const UserPhotoCreate = () => {
  const [cropState, setCropState] = useState()
  const [finishState, setFinishState] = useState('')
  const [stepState, setStepState] = useState('')
  const [ displayStyle, setDisplayStyle ] = useState('block');
  const [img, setImg] = useState()
  const cropperRef = useRef()
  
  const onDragStop = useCallback((s) => setCropState(s), [])
  const onChange = useCallback((s) => setCropState(s), [])

  const nextStep = async () => {

      try {
        const res = await cropperRef.current.done({
          preview: true,
          filterCvParams: {
            th: false,
            thMeanCorrection: 13,
            thMode: window.cv.ADAPTIVE_THRESH_GAUSSIAN_C,
            grayScale: false
          }
        })   
        
        if (!stepState){   
          file_k = URL.createObjectURL(new File([res], "my_image.png",{type:"image/png", lastModified:new Date().getTime()}))       
          setDisplayStyle('none')
          setStepState(file_k);
        } else { 
          setStepState();     
          setFinishState(1);
          setCropState();
          setDisplayStyle('none');         
        }      
       
      
      } catch (e) {
        console.log('error', e)
      }
    
  }

  const onImgSelection = async (e) => {
    if (e.fileList && e.fileList.length > 0) {

      const width = await checkImageWidth(e.fileList[0].originFileObj);

      if (width <= 1440) {
        setImg(e.fileList[0].originFileObj)
      } else {
       const resizedImage = await resizeImage(e.fileList[0].originFileObj);
        const resizedFile = {
          file: {
            originFileObj: resizedImage,
          }
        };

        //console.log(new File([resizedImage], "my_image.jpg",{type:"image/jpeg", lastModified:new Date().getTime()}));

        //console.log(resizedFile.originFileObj);
        
        setImg(new File([resizedImage], "my_image.jpg",{type:"image/jpeg", lastModified:new Date().getTime()}))

      }
      //console.log(new File([resizedImage], "my_image.jpg",{type:"image/jpeg", lastModified:new Date().getTime()}));

      //console.log(width);
      

      // it can also be a http or base64 string for example
      
    }

    console.log(img);
  }

  const draggerProps = {
    name: 'file',
    multiple: false,
    onChange: onImgSelection
  }

  return (
    <div className='root-container'>
      
      <div className='content-container'>
        {cropState && (
          <div className='buttons-container'>
            {/*  <Button
              onClick={() => {
                cropperRef.current.backToCrop()
                
                
                if (finishState){
                  setDisplayStyle('none')
                  setFinishState()
                  console.log(file_k);
                  setStepState(file_k)            
                } else if (stepState) {
                  setDisplayStyle('block')
                  setStepState()
                  setFinishState()
                }
                
                //setFinishState()
                
         
              }}
            >
              Back
            </Button> */}
            <Button onClick={nextStep} icon={<AiOutlineCheck />} className="save-img">
              Next
            </Button>      
           
            
          </div>
        )}
        <div className='cropper' style={{ 'display': displayStyle, 'margin': '16px 0' }}>
        <Cropper
          openCvPath='../opencv/opencv.js'
          ref={cropperRef}
          image={img}
          onChange={onChange}
          onDragStop={onDragStop}
          //maxWidth={window.innerWidth - 10}
        /></div>
        {cropState?.loading && <Loading />}
        {!img && !finishState && (
          <Dragger {...draggerProps}>
            <div className='drag_div'>
                <div>
                  <AiOutlineCamera size={40} />
                </div>
                <div className='title_upload'>Create Picture</div>
            </div>            
          </Dragger>
        )}
        
      </div>

      {stepState && (<UserPhotoFilter name={stepState} />) }

      {finishState?.loading && (<Loading />)}
      {finishState && (<UserPhotoPost name={finishState} />) }

      
      <div id='canvas_content'></div>
    
    </div>
    
  )
};

export default UserPhotoCreate;
