import Resizer from "react-image-file-resizer";
const resizeImage = (file) => {
  let quality = 85;
//4MB image file
  if (file.size > 4000000) {
    quality = 80;
  }
//8MB image file
  if (file.size > 8000000) {
    quality = 70;
  }
  return new Promise((resolve) => {
    Resizer.imageFileResizer(
      file,
      3000,
      3000,
      "JPEG",
      quality,
      0,
      (uri) => {
        resolve(uri);
      },
      "blob"
    );
  });
};
export { resizeImage };